.home {     
    display: flex;     
    justify-content: space-between;     
    align-items: center;     
    padding: 2rem 10%;     
    background-color: #f4f4f4;     
    min-height: 100vh;     
    width: 100%;     
    box-sizing: border-box; 
}  

.left-side {     
    flex: 1;     
    text-align: left;     
    padding-right: 2rem; 
}  

.left-side h1 {     
    font-size: 3rem;     
    color: #333;     
    margin-top: 0; 
}  

.running-lines {     
    font-size: 1.2rem;     
    color: black;     
    font-style: italic;     
    margin-top: 1rem; 
}  

.right-side {     
    flex: 1;     
    display: flex;     
    justify-content: center;     
    align-items: center; 
}  

.profile-image {     
    width: 550px;     
    height: 550px;     
    border-radius: 50%;     
    border: 5px solid #333;     
    object-fit: cover; 
}  

.name {     
    color: #3498db;     
    font-weight: bold; 
}  

.social-icons {     
    display: flex;     
    justify-content: flex-start;     
    margin-top: 20px; 
}  

.icon-circle {     
    display: flex;     
    justify-content: center;     
    align-items: center;     
    width: 50px;     
    height: 50px;     
    border-radius: 50%;     
    background-color: #f0f0f0;     
    margin: 0 10px;     
    transition: background-color 0.3s, transform 0.3s;     
    text-decoration: none;     
    color: #333; 
}  

.icon-circle:hover {     
    background-color: #3498db;     
    transform: scale(1.1);     
    color: white; 
}  
